	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/using-git-hooks-for-project-deployment-wins/index.md absPath of file >>> MarkdownRemark","html":"<p>I'm amazed that I've never really used this before, but as well as using **Git **to manage your version history, you can also tap into built-in Git hooks to copy your project source code into another location after you've pushed your updates.</p>\n<p>Essentially it's a cheap deployment process, and allows you to keep your source code repository and all its gubbins in one location, and serve your website from another.</p>\n<p>DigitalOcean has an excellent tutorial which runs you through setting up this kind of thing on a Linux-based box, and that's available here: <a href=\"https://www.digitalocean.com/community/articles/how-to-set-up-automatic-deployment-with-git-with-a-vps\">How to set up Automatic Deployment with Git with a VPS.</a></p>","frontmatter":{"title":"Using Git Hooks for Project Deployment wins","date":"February 09, 2014","tags":["git"],"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/using-google-analytics-in-flash/","tags":["actionscript","flash","google-analytics"],"title":"Using Google Analytics in Flash","image":null,"date":"2009-01-06T09:20:18.000Z"}},"next":{"frontmatter":{"path":"/blog/uploading-files-to-trello-with-node-js/","tags":["nodejs","trello"],"title":"Uploading files to Trello with Node.js","image":null,"date":"2017-05-18T20:38:43.000Z"}}}}

